import React from 'react';
import './SerachMusic.css';

const icon = require('../../img/serach.png');

const SerachMusic = () => (
  <div className="serachMusic">
    <div className="serachMusic_serach">
      <div><img src={icon} alt="" /></div>
      <input type="text" />
      <button></button>
    </div>
    <div />
    <p>5</p>
    <div />
  </div>
);
export default SerachMusic;



